class Lodings extends Component {
    //_this = this;
    constructor(props) {
        super(props);

    }
    componentWillMount(){
        this.state = {
            animating: false,
        };
    }
    componentWillUnmount() {
        clearTimeout(this.closeTimer);
    }
    showToast () {
        //alert('1111')
        console.log(this)
        this.setState({ animating: !this.state.animating });
        this.closeTimer = setTimeout(() => {
            this.setState({ animating: !this.state.animating });
        }, 1000);
    }
    render() {
        return (
            <div>
                <WingBlank>
                    <div className="loading-container">
                        <p className="sub-title">icon</p>
                        <div className="loading-example">
                            <ActivityIndicator animating />
                        </div>
                        <WhiteSpace size="xl" />
                        <p className="sub-title">icon</p>
                        <div className="loading-example">
                            <ActivityIndicator
                                text="..."
                            />
                        </div>
                        <WhiteSpace size="xl" />
                        <p className="sub-title">icon</p>
                        <div className="loading-example">
                            <div className="align">
                                <ActivityIndicator size="large" />
                                <span style={{ marginTop: 8 }}>...</span>
                            </div>
                        </div>
                    </div>
                    <div className="toast-container">
                        <WhiteSpace size="xl" />
                        <Button onClick={this.showToast.bind(this)}> Toast</Button>
                        <div className="toast-example">
                            <ActivityIndicator
                                toast
                                text=""
                                animating={this.state.animating}
                            />
                        </div>
                    </div>
                </WingBlank>
            </div>
        );
    }
}
