class ProgressLys extends Component{
    constructor(){
        super()
    }
    componentWillMount(){
        this.state={
            percent:60
        }

    }
    componentDidMount(){
        //
        componentStore.set(this);
    }
    componentWillUnmount(){
        //  start
        componentStore.clear(this);
    }
    //
    add(){
        let activState = this.state.percent+10;
        if(this.state.percent>=100){
            activState=0;
        }
        this.setState({
            percent:activState
        })

    }
    render(){
       // console.log(this)
        let progressNum=this.state.percent;
        return (
            <div>
                <div className="progress-container">
                    <div className="show-info">
                        <div className="progress"><Progress percent={progressNum} position="normal" /></div>
                        <div>{progressNum}%</div>
                    </div>
                    <Button inline style={{ marginTop: 20 }} onClick={this.add.bind(this)}>10</Button>
                </div>
            </div>
        )
    }
}

