class TestBottom extends Component{
    constructor(){
        super()
    }

    componentWillMount(){
        _that=this;
        this.state = {
            sel: '',
        };
    }
    componentDidMdount(){
        componentStore.set(this)
    }
    componentWillUnmount(){
        componentStore.clear(this)
    }
    /*   <span></span><i></i>   */
    onClick () {
        Popup.show(<div>
            <List renderHeader={() => (
                <div style={{ position: 'relative' }}>
                    
                    <span
                        style={{
                            position: 'absolute', right: 3, top: -5,
                        }}
                        onClick={() => _that.onClose('cancel')}
                    >
            <Icon type="cross" />
          </span>
                </div>)}
                  className="popup-list"
            >
                {['', '', '', '', ''].map((i, index) => (
                    <List.Item key={index}>{i}</List.Item>
                ))}
            </List>
            <ul style={{ padding: '0.18rem 0.3rem', listStyle: 'none' }}>
                <li>...</li>
                <li style={{ marginTop: '0.18rem' }}>
                    <Button type="primary" onClick={() => _that.onClose('cancel')}></Button>
                </li>
            </ul>
        </div>, { animationType: 'slide-up', maskProps, maskClosable: false });
    };
    onClose (sel){
        this.setState({ sel });
        Popup.hide();
    };
    render() {
        return (<div style={{ padding: '0.15rem' }}>
            <Button onClick={this.onClick}></Button>
        </div>);
    }
}
